import * as React from "react";
// import "bootstrap/dist/css/bootstrap.min.css";
import Layout from "../components/layout";
import HeroMarquee from "../components/heroMarquee/heroMarquee";
import HeroBrandText from "../components/index/heroBrandText";
import HeroHighlight from "../components/index/heroHighlight";

const bodyStyles = {
  fontFamily: "Playfair Display",
};

const IndexPage = () => {
  return (
    <Layout pageTitle="Diggital" style={bodyStyles}>
      <HeroHighlight />
      <div className={`row`}>
        <div className="col">
          <HeroMarquee />
        </div>
      </div>
      <HeroBrandText />
    </Layout>
  );
};

export const Head = () => <title>Diggital</title>;

export default IndexPage;
