import * as React from "react";
import * as styles from "./HeroMarquee.module.css";
import { StaticImage } from "gatsby-plugin-image";
import Marquee from "react-fast-marquee";
import laneImg from "../../images/products/Lane.png";
import seltzerImg from "../../images/products/Seltzer.png";
import sippyImg from "../../images/products/Sippy.png";
import snowLeopardImg from "../../images/products/SnowLeopard.png";
import vivaCiderImg from "../../images/products/VIVA_Cider.png";

export default function HeroMarquee() {
  const widt = 300;
  const heig = 400;
  const items = [
    {
      id: 1,
      fileSrc: laneImg,
      label: "Lane",
      width: widt,
      height: heig,
    },
    {
      id: 2,
      fileSrc: seltzerImg,
      label: "Seltzer",
      width: widt,
      height: heig,
    },
    {
      id: 3,
      fileSrc: sippyImg,
      label: "Sippy",
      width: widt,
      height: heig,
    },
    {
      id: 4,
      fileSrc: snowLeopardImg,
      label: "SnowLeopard",
      width: widt,
      height: heig,
    },
    {
      id: 5,
      fileSrc: vivaCiderImg,
      label: "VIVA Cider",
      width: widt + widt,
      height: heig,
    },
  ];

  return (
    <Marquee>
      {items.map((item, idx) => {
        return (
          <span
            data-bs-toggle="tooltip"
            data-bs-placement="top"
            data-bs-title="Tooltip on top"
            key={idx}
          >
            <img
              alt={item.label}
              src={item.fileSrc}
              width={item.widt}
              height={item.heig}
            />
          </span>
        );
      })}
    </Marquee>
  );
}
